@import 'https://fonts.googleapis.com/css?family=Montserrat:300, 400, 700&display=swap';

* {
    margin: 0;
}

html,
body {
    overflow-x: hidden;
    font-family: 'Montserrat', sans-serif;
	scroll-behavior: smooth;
    background-color: #303030;
}

#articles-container {
    position: relative;
    overflow: hidden;
    max-width: 1100px;
    margin: 0 auto;
}

article {
    position: relative;
}

.slick-track {
    display: flex;
    align-items: center;
}

.slick-slide {
    position: relative;
    display: flex;
    align-items: flex-end;
}

.slick-slide img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.baseboard {
    position: absolute;
    z-index: 1;
    color: whitesmoke;
    background-color: rgba(37, 37, 37, 0.733);
    width: 100%;
    padding: 10px;
    overflow: hidden;
    max-height: 200px;
    white-space: pre-wrap;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    box-sizing: border-box;
    transition: max-height 0.3s ease-in-out;
    text-align: center;
}

.baseboard h2,
.baseboard p {
    display: inline-block;
}

@media (max-width: 768px) {
    .baseboard {
        max-height: none;
    }
}

.slick-arrow {
    background-color: rgba(0, 0, 0, 0.5);
    border: none;
    color: white;
    font-size: 1.5rem;
    line-height: 1;
    padding: 15px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
}

.slick-prev {
    left: 10px;
    border-radius: 90px;
}

.slick-next {
    right: 10px;
    border-radius: 90px;
}

.header {
    background: rgb(0, 0, 0);
    background: linear-gradient(29deg, rgba(0, 0, 0, 1) 78%, rgba(119, 0, 202, 1) 100%);
    color: white;
    position: relative;
}

.headerTop {
    display: flex;
}

.headerTop h1 span {
    color: #7700CA;
}

#name {
    position: relative;
    top: 16px;
    font-family: 'Arvo', serif;
    font-family: 'Caveat', cursive;
}

.headerBottom {
    display: flex;
    justify-content: flex-end;
    padding-left: 10%;
    padding: 2px;
}

#logo {
    height: 50px;
    position: static;
    border: 8px solid black;
    border-radius: 90px;
}

button {
    appearance: none;
    background: transparent;
    border: 0;
    color: #fff;
    cursor: pointer;
    font: inherit;
    font-weight: 500;
    line-height: 0.5;
    padding: 1em 1.5em;
    position: relative;
    transition: filter var(--motion-duration);
    margin-left: 5px;
}

button:hover {
    filter: brightness(1.1);
}

button:active {
    filter: brightness(0.9);
}

button>span {
    display: block;
    position: relative;
    transition: transform var(--motion-duration) var(--motion-ease);
    z-index: 1;
}

button:hover>span {
    transform: scale(1.05);
}

button:active>span {
    transform: scale(0.95);
}

button>svg {
    fill: #363636b4;
    position: absolute;
    top: -5%;
    left: -5%;
    width: 110%;
    height: 110%;
}

button>svg>path {
    transition: var(--motion-duration) var(--motion-ease);
}

button:hover>svg>path {
    d: path("M0,0 C0,-5 100,-5 100,0 C105,0 105,100 100,100 C100,105 0,105 0,100 C-5,100 -5,0 0,0");
}

button:active>svg>path {
    d: path("M0,0 C30,10 70,10 100,0 C95,30 95,70 100,100 C70,90 30,90 0,100 C5,70 5,30 0,0");
}



/* Footer */
#footer {
    background-image: linear-gradient(60deg, #000000 0%, #272727 100%);
    display: flex;
    text-align: center;
    justify-content: center;
}

#footer .footer {
    min-height: 150px;
    flex-direction: column;
    padding-top: 50px;
    padding-bottom: 10px;
}

#footer h2 {
    color: white;
    font-weight: 500;
    font-size: 1.8rem;
    letter-spacing: 0.1rem;
    margin-top: 10px;
    margin-bottom: 10px;
}

#footer .social-icon {
    display: flex;
    margin-bottom: 30px;
    margin-top: 10px;
    justify-content: center;
}

#footer .social-item {
    height: 60px;
    width: 60px;
    margin: 0 5px;
}

#footer .social-item img {
    filter: grayscale(1);
    transition: 0.3s ease filter;
    height: 100%;
    width: 100%;
}

#footer .social-item:hover img {
    filter: grayscale(0);
}

#footer p {
    color: white;
    font-size: 1.3rem;
}

#footer .brand h1 span {
    color: #7700CA;
    font-weight: 400;
}

#footer .brand h1 {
    color: white;
    font-weight: 300;
}

/* End Footer */